@keyframes slideInFromLeft {
    0% {
        transform: translateX(-150%); }
    100% {
        transform: translateX(0); } }
@keyframes gradient {
	0% {
		background-position: 0% 50%; }
	50% {
		background-position: 100% 50%; }
	100% {
		background-position: 0% 50%; } }
body {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    padding: 0 8vw;
    color: $text95;
    -webkit-font-smoothing: subpixel-antialiased;
    @include bp-small {
        padding: 0 18px; } }
header {
    width: 60%;
    @include bp-small {
        width: 100%; }
    h3 {
        color: $text65;
        letter-spacing: 1px;
        font-weight: 400;
        line-height: 166%;
        padding-bottom: 10vh; } }
hr {
    background: linear-gradient(90deg, $secondary, $accent);
    height: 2px;
    width: 100%;
    border: 0;
    animation: gradient 10s ease infinite;
    background-size: 200% 200%; }
.case-studies-text {
    max-width: 75ch;
    margin: 0 auto; }
.case-studies {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .case-study {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 30vw;
        text-align: center;
        @include bp-small {
            min-height: 90vw; } }
    a {
        text-decoration: none;
        color: inherit;
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        transition: background .1s ease-in-out;
        &:hover {
            background: $text02; } }
    p {} }

.about {
    display: flex;
    flex-direction: row;
    align-items: center;
    picture {
        border-radius: 50%;
        object-fit: cover;
        padding: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        @include bp-small {
            padding: 2rem 0; } }
    @include bp-small {
        flex-direction: column; }
    .text {
        flex: 2;
        padding: 2rem;
        @include bp-small {
            padding: 0 2rem 0 0; } } }
.contact {
    flex-direction: row;
    display: flex;
    @include bp-small {
        flex-direction: column; }
    .contact-form, .contact-list {
        flex: 1;
        display: flex;
        justify-content: top;
        align-items: center;
        flex-direction: column;
        padding: 4rem;
        @include bp-small {
            padding: 2rem 0; }
        ul {
            padding-left: 0;
            li {
                list-style: none;
                line-height: 3; }
            svg {
                margin-top: 1rem; } } } }
.images {
    display: flex;
    justify-content: center;
    flex-direction: column;
    img, source {
        object-fit: cover;
        width: 100%;
        padding-bottom: 1rem; }
    p {
        font-size: 0.9rem;
        font-style: italic;
        margin-top: 0; } }
footer {
    color: $text35;
    padding: 5vh 8vw;
    text-align: center;
    a {
        white-space: pre; }
    @include bp-small {
        padding: 5vh 0; } }
